import { Component, OnInit } from '@angular/core';
import {ProyectoService} from '../proyecto.service';

@Component({
  selector: 'app-form-proyecto',
  templateUrl: './form-proyecto.component.html',
  styleUrls: ['./form-proyecto.component.css']
})
export class FormProyectoComponent implements OnInit {


  ProyectoInfo: any = {};
  response: any;
  error: any;
  proyectos: any;
  tipos_proyecto: any;
  categoria_proyecto: any;

  constructor(private proyectoService: ProyectoService) { }

  ngOnInit() {
    this.getTiposProyecto();
    this.getCategoriaProyecto();
  }

  getAllProyecto(){
    this.proyectoService.getAllProyecto()
      .subscribe( proyectos => {
        this.proyectos = proyectos;
      });
  }

  createProyecto(){
    console.log(this.ProyectoInfo);
    this.proyectoService.createProyecto(this.ProyectoInfo)
      .subscribe( proyecto => {
        this.error = null;
        this.response = proyecto;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

  getTiposProyecto(){
    this.proyectoService.getTiposProyecto()
      .subscribe(
        tipos => this.tipos_proyecto = tipos
      )
  }

  getCategoriaProyecto(){
    this.proyectoService.getCategoriaProyecto()
      .subscribe(
        cat => this.categoria_proyecto = cat
      )
  }

}
