import { Component, OnInit } from '@angular/core';
import {TrabajoService} from '../trabajo.service';

@Component({
  selector: 'app-form-trabajo',
  templateUrl: './form-trabajo.component.html',
  styleUrls: ['./form-trabajo.component.css']
})
export class FormTrabajoComponent implements OnInit {


  PostulacionInfo: any = {};
  response: any;
  error: any;
  postulaciones: any;
  categoria_trabajo: any;

  constructor(private trabajoService: TrabajoService) { }

  ngOnInit() {

    this.getCategoriaTrabajo();
  }

  getAllPostulacion(){
    this.trabajoService.getAllPostulacion()
      .subscribe( posts => {
        this.postulaciones = posts;
      });
  }

  createOferta(){
  console.log(this.PostulacionInfo);
    this.trabajoService.createOferta(this.PostulacionInfo)
      .subscribe( post => {
        this.error = null;
        this.response = post;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

  getCategoriaTrabajo(){
    this.trabajoService.getCategoriaTrabajo()
      .subscribe(
        cat => {
          this.categoria_trabajo = cat;
        },
        err => {
          console.log(err);
        }
      )
  }

}
