import { Component, OnInit } from '@angular/core';
import { CursoService } from '../curso.service';

@Component({
  selector: 'app-form-curso',
  templateUrl: './form-curso.component.html',
  styleUrls: ['./form-curso.component.css']
})
export class FormCursoComponent implements OnInit {

  cursoInfo: any = {};
  response: any;
  error: any;
  cursos: any;
  tipos_cursos: any;

  constructor(private cursoService: CursoService) { }

  ngOnInit() {
    this.getTiposCurso()
      .subscribe(tipo => {
        this.tipos_cursos = tipo;
      })
  }

  getAllCursos(){
    this.cursoService.getAllCursos()
      .subscribe( cursos => {
        this.cursos = cursos;
      });
  }

  createCurso(){

    this.cursoService.createCursos(this.cursoInfo)
      .subscribe( curso => {
        this.error = null;
        this.response = curso;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

  getTiposCurso(){
    return this.cursoService.getCursoTipos();
  }

}
