import { Component, OnInit } from '@angular/core';
import {ArchivoService} from '../archivo.service';

@Component({
  selector: 'app-form-archivo',
  templateUrl: './form-archivo.component.html',
  styleUrls: ['./form-archivo.component.css']
})
export class FormArchivoComponent implements OnInit {

  archivoData: any = {};
  response: any;
  error: any;
  archivo: any;
  tipos_archivo: any;
  SelectedFile = null;

  constructor(private archivoService: ArchivoService) { }



  ngOnInit() {
    this.getTiposArchivo()
      .subscribe((archivos) => {
        this.tipos_archivo = archivos;
      })
  }

  submitForm(){
    console.log(this.archivoData);
    const fd = new FormData();
    fd.append('archivo', this.SelectedFile, this.SelectedFile.name);
    fd.append('nombre_archivo', this.archivoData.nombre_archivo);
    fd.append('descripcion', this.archivoData.descripcion);
    fd.append('id_tipo_archivo', this.archivoData.id_tipo_archivo);
    console.log(fd);
    this.archivoService.createArchivo(fd)
      .subscribe(
        res => {
          this.response = res;
        }
        ,
        err => {
          this.error = err;
        })
  }

  onFileSelected(event){
    console.log(event);
    this.SelectedFile = event.target.files[0];
  }

  getTiposArchivo(){
    return this.archivoService.getTiposArchivos();

  }




}
