import { Component, OnInit } from '@angular/core';
import {NoticiaService} from '../noticia.service';

@Component({
  selector: 'app-form-noticia',
  templateUrl: './form-noticia.component.html',
  styleUrls: ['./form-noticia.component.css']
})
export class FormNoticiaComponent implements OnInit {

  NoticiaInfo: any = {};
  response: any;
  error: any;
  noticias: any;
  namesImg: any;

  constructor(private noticiaService: NoticiaService) { }

  ngOnInit() {
  }

  getAllNoticia(){
    this.noticiaService.getAllNoticia()
      .subscribe( noticias => {
        this.noticias = noticias;
      });
  }

  createNoticia(){

    const fd = new FormData();
    fd.append('img', this.namesImg, this.namesImg.name);
    fd.append('titulo', this.NoticiaInfo.titulo);
    fd.append('descripcion', this.NoticiaInfo.descripcion);
    fd.append('p_fecha',  this.NoticiaInfo.p_fecha);
    this.noticiaService.createNoticia(fd)
      .subscribe(
        res => {
          this.response = res;
        }
        ,
        err => {
          this.error = err;
        })


  }

  onFileSelected(event){
    this.namesImg = event.target.files[0];
    console.log(this.namesImg);
  }



}
