import { Component, OnInit } from '@angular/core';
import {InhumacionService} from '../inhumacion.service';

@Component({
  selector: 'app-form-inhumacion',
  templateUrl: './form-inhumacion.component.html',
  styleUrls: ['./form-inhumacion.component.css']
})
export class FormInhumacionComponent implements OnInit {

  LoteInfo: any = {};
  response: any;
  error: any;
  inhumaciones: any;

  constructor(private inhumacionService: InhumacionService) { }

  ngOnInit() {
  }

  getAllInhumacion(){
    this.inhumacionService.getAllInhumacion()
      .subscribe( inhumaciones => {
        this.inhumaciones = inhumaciones;
      });
  }

  createInhumacion(){
    console.log(this.LoteInfo);
    this.inhumacionService.createInhumacion(this.LoteInfo)
      .subscribe( inhumacion => {
        this.error = null;
        this.response = inhumacion;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

}
