import { Component, OnInit } from '@angular/core';
import {BannersService} from '../banners.service';

@Component({
  selector: 'app-form-banner',
  templateUrl: './form-banner.component.html',
  styleUrls: ['./form-banner.component.css']
})
export class FormBannerComponent implements OnInit {

  Banner: any;
  SelectedFile: any ;
  exitoBorrar: any;
  success: any;

  constructor(private bannerService: BannersService) { }

  ngOnInit() {
    this.getBanners();
  }

  getBanners(){
    this.bannerService.getBanners()
      .subscribe(banner => {this.Banner = banner});
  }

  uploadBanner(){

    const fd = new FormData();
    fd.append('img', this.SelectedFile, this.SelectedFile.name);

    this.bannerService.uploadBanner(fd)
      .subscribe(
        banner => {this.success = banner},
        err => console.log(err)
        );
  }

  onFileSelected(event){
    console.log(event);
    this.SelectedFile = event.target.files[0];
  }

  deleteBanner(id) {
    this.bannerService.deleteBannerById(id)
      .subscribe(banner => {
        this.exitoBorrar = 'Se ha borrado con exito'
      });
  }


}
