import { Component, OnInit } from '@angular/core';
import {ServicioService} from '../servicio.service';


@Component({
  selector: 'app-form-servicio',
  templateUrl: './form-servicio.component.html',
  styleUrls: ['./form-servicio.component.css']
})
export class FormServicioComponent implements OnInit {

  servicioInfo: any = {};
  response: any;
  error: any;
  servicios: any;
  tipos_servicios: any;

  constructor(private servicioService: ServicioService) { }

  ngOnInit() {
    this.getTiposServicio();
  }

  getAllServicio(){
    this.servicioService.getAllServicio()
      .subscribe( servicios => {
        this.servicios = servicios;
      });
  }

  createServicio(){

    this.servicioService.createServicio(this.servicioInfo)
      .subscribe( curso => {
        this.error = null;
        this.response = curso;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

  getTiposServicio(){
    this.servicioService.getTiposServicio()
      .subscribe(
        tiposServicio => {
          this.tipos_servicios = tiposServicio;
        },
        err =>{
          console.log(err);
        }
      )
  }


}
