import { Component, OnInit } from '@angular/core';
import {LoteService} from '../lote.service';

@Component({
  selector: 'app-form-lote',
  templateUrl: './form-lote.component.html',
  styleUrls: ['./form-lote.component.css']
})
export class FormLoteComponent implements OnInit {


  LoteInfo: any = {};
  response: any;
  error: any;
  lotes: any;

  constructor(private loteService: LoteService) { }

  ngOnInit() {
  }

  getAllLote(){
    this.loteService.getAllLote()
      .subscribe( lotes => {
        this.lotes = lotes;
      });
  }

  createLote(){
    console.log(this.LoteInfo);
    this.loteService.createLote(this.LoteInfo)
      .subscribe( lote => {
        this.error = null;
        this.response = lote;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

}
