module.exports = ".grid-container {\n  display: -ms-grid;\n  display: grid;\n  grid-gap: 10px;\n  background-color: #2196F3;\n  padding: 10px;\n}\n\n.grid-item {\n  background-color: rgba(255, 255, 255, 0.8);\n  text-align: center;\n  padding: 20px;\n  font-size: 30px;\n}\n\n.item1 {\n  -ms-grid-column: 1;\n  -ms-grid-column-span: 1;\n  grid-column: 1 / span 1;\n  -ms-grid-row: 1;\n  grid-row: 1;\n}\n\n.item2 {\n  -ms-grid-column: 3;\n  grid-column: 3;\n  -ms-grid-row: 1;\n  -ms-grid-row-span: 2;\n  grid-row: 1 / span 2;\n}\n\n.item3{\n\n}\n\n.item4{\n\n}\n\n.item5 {\n  -ms-grid-column: 1;\n  -ms-grid-column-span: 3;\n  grid-column: 1 / span 3;\n   -ms-grid-row: 3;\n   grid-row: 3;\n}\n\n.jasgrid{padding:0;}\n\n.box-item {\n    float: left;\n    opacity: 1;\n    overflow: hidden;\n    position: relative;\n}\n\n.box-item img {\n    width: 100%;\n}\n\n.box-item a,span{color:#FFF;}\n\n.box-item .box-post span.meta {\n    font-family:  sans-serif;\n    font-size: 12px;\n    color: #fff;\n    margin-top: 15px;\n    display: block;\n}\n\n.box-item .box-post span.meta span {\n    margin-right: 15px;\n}\n\n.box-item .box-post {\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);\n    padding: 30px;\n    box-sizing: border-box;\n}\n\n.box-item .box-post h1..post-title {\n   font-size:10pt;\n}\n\n/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */\n\n@media screen and (max-width: 990px) {\n  .res-txt{\n    font-size: 2.3vw;\n  }\n}\n\n/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */\n\n@media screen and (max-width: 768px) {\n  .res-txt{\n    font-size: 5vw;\n  }\n}\n"