import { Injectable } from '@angular/core';
import {URL} from '../util/url';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class TrabajoService {


  url: string = `${URL}/api/postulacion/crear_postulacion`;
  url2: string = `${URL}/api/postulacion`;
  url3: string = `${URL}/api/categoria_trabajo`;
  url4: string = `${URL}/api/oferta_trabajo/crear_trabajo`;

  constructor(private http: HttpClient) {
  }

  createOferta(oferta){
    return this.http.post<any>(this.url4, oferta);
  }

  createPostulacion(postulacion) {
    return this.http.post<any>(this.url, postulacion);
  }

  getAllPostulacion(){
    return this.http.get(this.url2);
  }

  getCategoriaTrabajo(){
    return this.http.get(this.url3);
  }
}
