import { Injectable } from '@angular/core';
import {URL} from '../util/url';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ProyectoService {

  url: string = `${URL}/api/proyecto/crear_proyecto`;
  url2: string = `${URL}/api/proyecto`;
  url3: any = `${URL}/api/tipo_proyecto`;
  url4: any = `${URL}/api/categoria_proyecto`;

  constructor(private http: HttpClient) {
  }

  createProyecto(proyecto) {
    return this.http.post<any>(this.url, proyecto);
  }

  getAllProyecto() {
    return this.http.get(this.url2);
  }

  getTiposProyecto(){
    return this.http.get(this.url3);
  }

  getCategoriaProyecto(){
    return this.http.get(this.url4);
  }
}
